import {useState} from 'react'

const AddTodo = ({addTodo}) => {
    const [title, setTitle] = useState('')
    const handleSubmit = (e) => {
        e.preventDefault()
        addTodo(title)
        setTitle('')
    }
    return(
        <div className="card mb-3">
        <div className="card-body">
            <h5 className="card-title mb-2 list-title"><i className="far fa-calendar-plus"></i> Add Todo</h5>
            <form onSubmit={handleSubmit}>
                <div className="input-group mb-3">
                    <input type="text" className="form-control" value={title} onChange={(e) => setTitle(e.target.value)} placeholder="Add Todo..." required />
                    <button className="btn btn-outline-success" type="submit" id="button-addon2"><i className="fas fa-plus-circle"></i></button>
                </div>
            </form>
        </div>
    </div>
    )
}

export default AddTodo