const TodoList = ({todos, deleteTodo, completeTodo}) => {
    return(
        <div className="card">
        <div className="card-body">
            <h5 className="card-title mb-2 list-title"><i className="fas fa-clipboard-list"></i> Todos List</h5>

            <div className="my-3">
                <ul className="list-group" >
                    {todos.map(todo => (
                        <li className="list-group-item d-flex justify-content-between align-items-center" key={todo._id}>
                            <span>
                                <input className="form-check-input me-1" type="checkbox" value="" defaultChecked={todo.complete} onChange={() => completeTodo({complete: !todo.complete, id: todo._id})}  />
                                {todo.title}  <span className="badge bg-dark mx-3"><i className="fas fa-calendar-alt"></i> {new Date(todo.date).toLocaleString('en-eg')}</span>
                            </span>
                            <span>
                                <button type="button" className="btn btn-outline-danger btn-sm" onClick={() => deleteTodo(todo._id)} ><i className="fas fa-trash-alt"></i></button>
                            </span>
                        </li>
                    ))}
                </ul>
            </div>
            
        </div>
    </div>
    )
}

export default TodoList