import {useState, useEffect} from 'react'
import AddTodo from "./AddTodo"
import Spinner from './Spinner'
import TodoList from "./TodoList"

const Main = () => {
    const [todos, setTodos] = useState([])
    const [showAdd, setShowAdd] = useState(false)

    useEffect(() => {

        const getTodos = async () => {
            const res = await fetch('https://vrn-todos.herokuapp.com/api/todos')
            const data = await res.json()
            setTodos(data.data)
        }
        getTodos()

    }, [])

    const addTodo = async (title) => {
        try {
            const res = await fetch('https://vrn-todos.herokuapp.com/api/todos', {
                method: "POST",
                headers: {"Content-Type": "application/json"},
                body: JSON.stringify({title})
            })
            const todo = await res.json()

            setTodos(prev => [todo.data, ...prev])
        } catch (error) {
            if(error) throw error
        }

    }
    const deleteTodo = async (id) => {
        await fetch(`https://vrn-todos.herokuapp.com/api/todos/${id}`, {
                method: "DELETE",
                headers: {"Content-Type": "application/json"}
            })

        setTodos(prev => prev.filter(todo => todo._id !== id))
    }
    const completeTodo = async (data) => {
        await fetch(`https://vrn-todos.herokuapp.com/api/todos/${data.id}`, {
                method: "PUT",
                headers: {"Content-Type": "application/json"},
                body: JSON.stringify({complete:data.complete})
            })
    }
    
    return (
        <>
            <div className="card mb-3 bg-info text-light">
                <div className="card-header">
                    <i className="fas fa-user-circle"></i> Hi, User You can add Todo
                </div>
                <div className="card-body d-flex justify-content-center">
                    {showAdd ? (<span className="todo-button btn btn-danger" onClick={() => setShowAdd(false)}  ><i className="fas fa-times"></i></span>) : (<span className="todo-button btn btn-primary" onClick={() => setShowAdd(true)} ><i className="fas fa-plus"></i></span>) }
                    
                    
                </div>
            </div>
            {showAdd && (<AddTodo addTodo={addTodo} />)}
            {todos.length > 0 ? (<TodoList todos={todos} deleteTodo={deleteTodo} completeTodo={completeTodo} />) : (<Spinner />)}
        </>
    )
}

export default Main